.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  /* margin: 0px; */
  /* Who needs margin of 2%? */
  margin: 2%;
}
html {
  height: 100%;
}
body {
  min-height: 100%;
}

.pageContent {
  display: flex;
  flex-direction: column;
  margin-bottom: 5%;
}

#map {
  display: flex;
  flex-direction: column;
  margin-top: 3%;
}
.mapboxgl-canvas {
  display: flex;
  flex-direction: column;
  background-color: lightgray;
  box-shadow: 10px 10px grey;
  border-radius: 10px;
  box-shadow: 10px 10px grey;
}
.calculateCO2 {
  display: inline-block;
  margin-bottom: 3%;
  box-sizing: border-box;

  text-transform: uppercase;

  font-weight: 400;
  color: black;
  text-align: center;
  background-color: lightgreen;

  border-radius: 10px;
  padding: 10px;
  width: 500px;
}

.layout {
  display: flex;
  flex-direction: column;
  margin-top: 2%;
  align-items: center;
  width: 58%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
